.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

.App-header {
    width: 100%;
    background-color: #84adff;
    min-height: 20vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    font-family: "Monaco", monospace;
    color: white;
}

.App-welcomeHeader {
    text-align: center;
    background-color: rgb(176, 208, 238);
    padding: 10px;
}

.App-columns {
    text-align: center;
    color: white;
    width: 100%;
}

.App-columnLeft {
    height: 70vh;
    position: relative;
    background-color: #7189bb;
}

.App-columnRight {
    position: relative;
    background-color: #5f77a8;
}

.App-image {
    position: absolute;
    top: 20%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 50%;
    height: 25%;
}

.App-listHeader {
    position: absolute;
    top: 45%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 25px;
}

.App-list {
    position: absolute;
    top: 60%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 20px;
}

.App-rectangles {
    position: absolute;
    top: 90%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 75%;
    height: 15%;
    background-color: red;
}

.App-link {
    color: #61dafb;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}
